.bMediaUpload {
  position: relative;
  margin-bottom: 12px;
}
.bMediaUpload button.button-primary {
  position: absolute;
  right: 0px;
  min-height: 31px;
  padding: 3px !important;
  color: #fff !important;
  outline: 0;
  bottom: 0px; 
  border-radius: 3px !important;
}
.block-editor-block-inspector .bMediaUpload .components-base-control {
  width: calc(100% - 40px);
  margin-bottom: 0px;
}

.bMediaUpload .components-base-control {
  width: calc(100% - 40px);
}

.bMediaUpload button:focus {
  outline: 0;
  box-shadow: 0 0 0 0px #007cba !important;
}
.bMediaUpload .components-panel__row {
  min-height: 30px;
}

.bMediaUpload label {
  margin-top: 3px;
  display: inline-block;
  margin-bottom: 5px;
}

.bMediaUpload .components-base-control input {
  padding-right: 0px;
}
