.modalContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 999999;
    .overlay {
      width: 100%;
      height: 100%;
      background: rgb(28 27 27 / 88%);
    }
    .contentContainer {
      z-index: 999;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: #fff;
      padding: 15px;
      border-radius: 3px;
      padding-top: 30px;
      max-width: 100%;
      @media screen and (max-width: 576px) {
        max-height: calc(100vh - 70px);
        overflow: scroll;
      }
    }
    .close {
      font-size: 25px;
      line-height: 100%;
      position: absolute;
      top: 5px;
      right: 5px;
      background: #333;
      color: #fff;
      padding: 3.5px 9px;
      display: inline-block;
      font-family: sans-serif;
      cursor: pointer;
      margin: 0;
      width: auto;
      height: auto;
      border-radius: 3px;
    }
  }